'use client';

import { useEffect, useState } from 'react';
import { Plus, MessageCircle, Trash2 } from 'lucide-react';

interface HistoryItem {
  role: string;
  transcript: string;
  audio_url: string | null;
  created_at: string;
}

interface SidebarProps {
  sessionId: string | null;
  onNewChat: () => void;
}

export default function Sidebar({ sessionId, onNewChat }: SidebarProps) {
  const [history, setHistory] = useState<HistoryItem[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (sessionId) {
      fetchHistory();
    }
  }, [sessionId]);

  const fetchHistory = async () => {
    if (!sessionId) return;
    setLoading(true);
    try {
      const response = await fetch(`http://localhost:5000/chat/history/${sessionId}`);
      const data = await response.json();
      setHistory(data || []);
    } catch (error) {
      console.error('Failed to fetch history:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="w-64 bg-gray-800 text-white flex flex-col border-r border-gray-700">
      {/* New Chat Button */}
      <button
        onClick={onNewChat}
        className="m-4 flex items-center justify-center gap-2 bg-green-600 hover:bg-green-700 text-white font-semibold py-2 px-4 rounded-lg transition"
      >
        <Plus size={20} />
        New Chat
      </button>

      {/* History Section */}
      <div className="flex-1 overflow-y-auto">
        <div className="px-4 py-2">
          <h2 className="text-xs uppercase tracking-wider text-gray-400 font-semibold">
            Conversation History
          </h2>
        </div>

        {loading ? (
          <div className="px-4 py-2 text-sm text-gray-400">Loading...</div>
        ) : history.length === 0 ? (
          <div className="px-4 py-2 text-sm text-gray-400">No messages yet</div>
        ) : (
          <div className="space-y-1">
            {history.map((item, index) => (
              <div
                key={index}
                className="mx-2 p-3 rounded-lg bg-gray-700 hover:bg-gray-600 cursor-pointer transition truncate text-sm"
              >
                <div className="flex items-start gap-2">
                  <MessageCircle size={14} className="mt-1 flex-shrink-0" />
                  <div className="flex-1 min-w-0">
                    <p className="font-semibold text-xs text-gray-300 capitalize">
                      {item.role}
                    </p>
                    <p className="text-xs text-gray-400 truncate">
                      {item.transcript || 'Audio message'}
                    </p>
                    <span className="text-xs text-gray-500">
                      {new Date(item.created_at).toLocaleTimeString()}
                    </span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Footer */}
      <div className="border-t border-gray-700 p-4">
        <button
          onClick={fetchHistory}
          className="w-full text-xs text-gray-400 hover:text-gray-200 transition"
        >
          Refresh History
        </button>
      </div>
    </div>
  );
}